import http from '../axios';
import * as rubixService from './rubixService';
import { RUBIX_APP_TYPES } from '../constants/appConstant';
import { exportENV, exportJSON, exportOpenVPNConfig } from '../utils/utils';

const endpoint = '/slave-apps';

export const fetch = () => {
  return http.get(`${endpoint}`, { restrictToast: true });
};

export const deleteSlaveAppsDownloadState = () => {};

function mapPayloadToRequestControlApps(dataItems, action) {
  return dataItems.reduce((acc, item) => {
    if (!acc[item.slaveId]) {
      acc[item.slaveId] = [];
    }
    acc[item.slaveId].push({
      service: item.service,
      action,
    });
    return acc;
  }, {});
}

function mapPayloadToRequestDownloadApps(dataItems) {
  return dataItems.reduce((acc, item) => {
    if (!acc[item.slaveId]) {
      acc[item.slaveId] = [];
    }
    acc[item.slaveId].push({
      service: item.service,
      version: item.version,
    });
    return acc;
  }, {});
}

export const bulkControlApps = (dataItems, action) => {
  return http.post(`${endpoint}/control`, mapPayloadToRequestControlApps(dataItems, action));
};

export const bulkDownloadSlaveApps = data => {
  return http.post(`${endpoint}/download`, mapPayloadToRequestDownloadApps(data));
};

export const bulkInstallSlaveApps = data => {
  return http.post(`${endpoint}/install`, mapPayloadToRequestDownloadApps(data));
};

export const fetchDownloadState = () => {
  return http.get(`${endpoint}/download-state`, { restrictToast: true });
};

export const clearDownloadState = slaveUUIDs => {
  return http.post(`${endpoint}/remove-download-state`, slaveUUIDs);
};

export const fetchAppReleases = serviceName => {
  return http.get(`${endpoint}/releases/${serviceName}?all=true`);
};

export const fetchAppsReleases = services => {
  return Promise.all(services.map(service => fetchAppReleases(service).catch(err => [])));
};

export const downloadAppData = async (serviceName, slaveId) => {
  const data = await rubixService.fetchAppUrl();
  const url = `${data.data}slave/${slaveId}/api/app/download_data?service=${serviceName}`;
  return http.get(url, {
    headers: {
      'Content-Type': 'application/zip',
    },
    responseType: 'blob',
  });
};

export const deleteAppData = (serviceName, slaveId) => {
  return http.post(`${endpoint}/delete-app-data`, { service: serviceName, slaveId });
};

export const fetchJSONConfig = (serviceName, slaveId, config) => {
  return http.get(`${endpoint}/slaves/${slaveId}/json-config/${serviceName}`, config);
};

export const fetchOpenVPNConfig = (serviceName, slaveId, config) => {
  return http.get(`${endpoint}/slaves/${slaveId}/open-vpn-config/${serviceName}`, config);
};

export const fetchEnvConfig = (serviceName, slaveId, config) => {
  return http.get(`${endpoint}/slaves/${slaveId}/env/${serviceName}`, config);
};

export const saveJSONConfig = (serviceName, config, slaveId) => {
  return http.put(`${endpoint}/slaves/${slaveId}/json-config`, { service: serviceName, data: config });
};

export const saveENVConfig = (serviceName, config, slaveId) => {
  return http.put(`${endpoint}/slaves/${slaveId}/env`, { service: serviceName, data: config });
};

export const saveOpenVPNConfig = (serviceName, config, slaveId) => {
  return http.put(`${endpoint}/slaves/${slaveId}/open-vpn-config`, { service: serviceName, data: config });
};

export const fetchAppConfig = (serviceName, appType, config) => {
  const slaveId = config.slaveId;

  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return fetchJSONConfig(serviceName, slaveId, config).then(({ data }) => data);
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return fetchEnvConfig(serviceName, slaveId, config).then(({ data }) => data);
  } else if (appType === RUBIX_APP_TYPES.APT_APP) {
    return fetchOpenVPNConfig(serviceName, slaveId, config).then(({ data }) => data);
  }
  throw new Error('App type not supported for editing config');
};

export const saveAppConfig = (serviceName, appType, config, slaveId) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return saveJSONConfig(serviceName, config || {}, slaveId);
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return saveENVConfig(serviceName, config || '', slaveId);
  } else if (appType === RUBIX_APP_TYPES.APT_APP) {
    return saveOpenVPNConfig(serviceName, config || '', slaveId);
  }
  throw new Error('App type not supported for editing config');
};

export const downloadAppConfig = (serviceName, slaveId, slaveDeviceName, appType, config = {}) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return fetchJSONConfig(serviceName, slaveId, config).then(({ data }) => {
      exportJSON(`${serviceName}-${slaveDeviceName}-config`, JSON.stringify(data));
    });
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return fetchEnvConfig(serviceName, slaveId, config).then(({ data }) => {
      exportENV(`${serviceName}-${slaveDeviceName}`, data);
    });
  } else if (appType === RUBIX_APP_TYPES.APT_APP) {
    return fetchOpenVPNConfig(serviceName, slaveId, config).then(({ data }) => {
      exportOpenVPNConfig(`${serviceName}-${slaveDeviceName}`, data);
    });
  }
  throw new Error('App type not supported for download config');
};

export const deleteJSONConfig = (slaveId, serviceName) => {
  return http.delete(`${endpoint}/slaves/${slaveId}/json-config`, { data: { service: serviceName } });
};

export const deleteENVConfig = (slaveId, serviceName) => {
  return http.delete(`${endpoint}/slaves/${slaveId}/env`, { data: { service: serviceName } });
};

export const deleteOpenVPNConfig = (slaveId, serviceName) => {
  return http.delete(`${endpoint}/slaves/${slaveId}/open-vpn-config`, { data: { service: serviceName } });
};

export const deleteAppConfig = (slaveId, serviceName, appType) => {
  if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return deleteJSONConfig(slaveId, serviceName);
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return deleteENVConfig(slaveId, serviceName);
  } else if (appType === RUBIX_APP_TYPES.APT_APP) {
    return deleteOpenVPNConfig(slaveId, serviceName);
  }
  throw new Error('App type not supported for deletion');
};

export const uninstallApp = (slaveId, serviceName) => {
  return http.post(`${endpoint}/uninstall`, { service: serviceName, slaveId });
};

export const changeServiceStatus = (action, serviceName, slaveId) => {
  let config = {
    restrictToast: true,
  };
  return http.post(`${endpoint}/slaves/${slaveId}/control`, [{ action, service: serviceName }], config);
};

export const createRestartJob = (body, slaveId) => {
  return http.post(`${endpoint}/slaves/${slaveId}/restart-job`, [body]);
};

export const deleteRestartJob = (body, slaveId) => {
  return http.delete(`${endpoint}/slaves/${slaveId}/restart-job`, { data: [body] });
};

export const uploadApp = (file, serviceName, version, slaveId) => {
  let formData = new FormData();
  formData.append('file', file);
  formData.append('service', serviceName);
  formData.append('version', version);
  return http.post(`${endpoint}/slaves/${slaveId}/app-upload`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
};

export const installApp = (body, slaveId) => {
  return http.post(`${endpoint}/slaves/${slaveId}/install`, [body]);
};

export const fetchAppStatus = (service, options = {}, slaveId) => {
  return http.get(`${endpoint}/slaves/${slaveId}/stats/${service}`, options);
};

export const fetchLatestAppVersion = () => {
  return http.get(`${endpoint}/latest-version`);
};

export const fetchSystemRebootJob = slaveId => {
  return http.get(`${endpoint}/slaves/${slaveId}/system-reboot`);
};

export const updateSystemRebootJob = (body = {}, slaveId) => {
  return http.put(`${endpoint}/slaves/${slaveId}/system-reboot`, body);
};

export const deleteSystemRebootJob = slaveId => {
  return http.delete(`${endpoint}/slaves/${slaveId}/system-reboot`);
};

export const bulkUpdateSystemRebootJob = (body = {}, slaveIds) => {
  return Promise.all(slaveIds.map(slaveId => updateSystemRebootJob(body, slaveId)));
};

export const bulkDeleteSystemRebootJob = slaveIds => {
  return Promise.all(slaveIds.map(slaveId => deleteSystemRebootJob(slaveId)));
};
