<template>
  <div class="service-cron-picker-wrapper">
    <v-subheader dense class="pl-0 pb-5"> Day(s) </v-subheader>
    <v-slider
      dense
      @input="() => onInput()"
      min="0"
      max="30"
      :thumb-size="20"
      v-model="day"
      thumb-label="always"
      track-color="grey"
    ></v-slider>
    <v-subheader dense class="pl-0 pb-5"> Hour(s) </v-subheader>

    <v-slider
      dense
      @input="() => onInput()"
      min="0"
      :thumb-size="20"
      max="23"
      v-model="hour"
      thumb-label="always"
      track-color="grey"
    ></v-slider>
    <v-subheader dense class="pl-0 pb-5"> Minute(s) </v-subheader>

    <v-slider
      dense
      @input="() => onInput()"
      min="0"
      max="59"
      :thumb-size="20"
      v-model="minute"
      thumb-label="always"
      track-color="grey"
    ></v-slider>
  </div>
</template>
<script>
const MINUTES = 60;
const HOURS_DAY = 24;
const MINUTES_DAY = HOURS_DAY * MINUTES;
export default {
  name: 'service-cron-picker-field',
  props: ['value'],
  data() {
    return {
      day: 0,
      hour: 0,
      minute: 0,
    };
  },
  computed: {
    _value: {
      get() {
        return this.value;
      },
      set(value) {
        return value;
      },
    },
  },
  mounted() {
    this.updateModel(this.value);
  },
  methods: {
    updateModel(val) {
      const { day, hour, minute } = this.splitMinutes(val);
      this.day = day;
      this.hour = hour;
      this.minute = minute;
    },
    splitMinutes(minutes) {
      return {
        day: this.getDay(minutes),
        hour: this.getHour(minutes),
        minute: this.getMinutes(minutes),
      };
    },
    getDay(minutes) {
      minutes = +minutes || 0;
      return Math.floor(+minutes / MINUTES_DAY) || 0;
    },
    getHour(minutes) {
      minutes = +minutes || 0;
      const exactDays = this.getDay(minutes);
      minutes = minutes - exactDays * MINUTES_DAY
      return Math.floor(minutes / MINUTES) || 0;
    },
    getMinutes(minutes) {
      minutes = +minutes || 0;
      const exactDays = this.getDay(minutes);
      minutes = minutes - exactDays * MINUTES_DAY
      const exactHours = this.getHour(minutes);
      minutes = minutes - exactHours * MINUTES;
      return minutes;
    },
    convertToMinutes() {
      const day = +this.day;
      const hour = +this.hour;
      const minute = +this.minute;
      return day * MINUTES_DAY + hour * MINUTES + minute;
    },
    onInput() {
      this.$emit('input', this.convertToMinutes());
    },
  },
};
</script>
<style>
.service-cron-picker-wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
}
</style>
