<template>
  <tooltip-icon-button
    :buttonSize="buttonSize"
    :btnIcon="btnIcon"
    :onClick="handleClick"
    :btnColor="timerBtnColor"
    :btnTooltip="timerBtnText"
  />
</template>
<script>
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';

export default {
  name: 'configure-cron-btn',
  props: ['service', 'handleClick', 'btnSize'],
  components: { TooltipIconButton },
  data() {
    return {
      btnIcon: 'fa-clock-o',
    };
  },
  computed: {
    buttonSize() {
      return !this.btnSize ? 'small' : this.btnSize;
    },
    isTimerConfigure() {
      if (this.service && this.service.reboot_job) {
        return !!(this.service && this.service.reboot_job && this.service.reboot_job.timer);
      }
      return !!(this.service && this.service.restart_job && this.service.restart_job.timer);
    },
    timerBtnColor() {
      return this.isTimerConfigure ? 'yellow darken-2' : 'nubeGreen';
    },
    timerBtnText() {
      return this.isTimerConfigure ? 'SET | Configure restart schedule' : 'UNSET | Configure restart schedule';
    },
  },
};
</script>
