<template>
  <v-tooltip bottom>
    <template v-slot:activator="{ on, attrs }">
      <v-btn :disabled="disabled" v-if="buttonSize == 'medium'" @click="onClick" icon medium :color="btnColor" v-bind="attrs" v-on="on">
        <v-icon medium dark> {{ btnIcon }} </v-icon>
      </v-btn>
      <v-btn :disabled="disabled" v-else @click="onClick" icon small :color="btnColor" v-bind="attrs" v-on="on">
        <v-icon small dark> {{ btnIcon }} </v-icon>
      </v-btn>
    </template>
    <span style="white-space: pre-line;" v-html="btnTooltip"/>
  </v-tooltip>
</template>
<script>
export default {
  name: 'tooltip-icon-button',
  props: ['btnIcon', 'btnTooltip', 'onClick', 'btnColor', 'buttonSize', 'disabled'],
};
</script>
