<template>
  <div class="text-left ma-5">
    <div v-if="isArray">
      <v-alert v-for="error in errors" :key="error.index" dense outlined type="error">
        <span>{{ error.message }}</span>
      </v-alert>
    </div>
    <div v-if="isObject && error.message">
      <v-alert dense outlined type="error">
        <span>{{ error.message }}</span>
      </v-alert>
    </div>
  </div>
</template>
<script>
export default {
  name: 'form-error',
  props: ['errors', 'error'],
  computed: {
    isArray() {
      return (Array.isArray(this.errors) && this.errors.length > 0) || false;
    },
    isObject() {
      return this.error && this.error.hasOwnProperty('message');
    },
  },
};
</script>
