import http from '../axios';

const endpoint = '/rubix-service';

export const fetchPlatInfo = () => {
  return http.get(`${endpoint}/plat`);
};

export const fetchStatus = () => {
  return http.get(`${endpoint}/status`);
};

export const upgradeLatest = () => {
  return http.put(`${endpoint}/upgrade`, { version: 'latest' });
};

export const upgrade = version => {
  return http.put(`${endpoint}/upgrade`, { version });
};

export const fetchReleases = () => {
  return http.get(`${endpoint}/releases`);
};

export const updateToken = token => {
  return http.post(`${endpoint}/token`, { token });
};

export const fetchTokenHint = () => {
  return http.get(`${endpoint}/token-hint`);
};

export const upgradeViaFile = (version, file) => {
  let formData = new FormData();
  formData.append('upgradeFile', file);
  formData.append('version', version);
  return http.post(`${endpoint}/upload-upgrade`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
};

export const fetchAppUrl = () => {
  return http.get(`${endpoint}/app-url`);
};

export const createRestartJob = body => {
  return http.post(`${endpoint}/restart-job`, [body]);
};

export const deleteRestartJob = body => {
  return http.delete(`${endpoint}/restart-job`, { data: [body] });
};

export const pingSystemService = () => {
  return http.get(`${endpoint}/system/ping`);
};

export const fetchSystemRebootJob = () => {
  return http.get(`${endpoint}/system-reboot`);
};

export const updateSystemRebootJob = (body = {}) => {
  return http.put(`${endpoint}/system-reboot`, body);
};

export const deleteSystemRebootJob = () => {
  return http.delete(`${endpoint}/system-reboot`);
};
