<template>
  <div>
    <div v-if="state">
      <span class="font-weight-medium">
        <v-chip v-if="chipSize && chipSize === 'small'" small outlined :color="currentStatus.color">{{
          currentStatus.displayName
        }}</v-chip>
        <v-chip v-else outlined :color="currentStatus.color">{{ currentStatus.displayName }}</v-chip>
      </span>
    </div>
    <div v-else>
      <span class="font-weight-medium">
        <v-chip v-if="chipSize && chipSize === 'small'" small outlined :color="currentStatus.color">{{
          defaultStatus
        }}</v-chip>
        <v-chip v-else outlined :color="currentStatus.color">{{ defaultStatus }}</v-chip>
      </span>
    </div>
  </div>
</template>
<script>
const appStatusStates = {
  active: {
    name: 'active',
    displayName: 'Running',
    outlined: true,
    color: 'exportColor',
  },
  reloading: {
    name: 'reloading',
    outlined: true,
    displayName: 'Reloading',
    color: 'nubeYellow',
  },
  failed: {
    name: 'failed',
    outlined: true,
    displayName: 'Failed',
    color: 'red',
  },
  activating: {
    name: 'activating',
    outlined: true,
    displayName: 'Activating',
    color: 'nubeYellow',
  },
  deactivating: {
    outlined: true,
    name: 'deactivating',
    displayName: 'Deactivating',
    color: 'nubeYellow',
  },
  maintenance: {
    name: 'maintenance',
    outlined: true,
    displayName: 'Maintenance',
    color: 'nubeYellow',
  },
  STARTED: {
    name: 'STARTED',
    outlined: true,
    displayName: 'Started',
    color: 'blue',
  },
  RUNNING: {
    name: 'RUNNING',
    outlined: true,
    displayName: 'Running',
    color: 'nubeYellow',
  },
  FINISHED: {
    name: 'FINISHED',
    outlined: true,
    displayName: 'Finished',
    color: 'nubeGreen',
  },
  BROKEN: {
    name: 'BROKEN',
    outlined: true,
    displayName: 'Broken',
    color: 'red',
  },
  MAPPED: {
    name: 'MAPPED',
    outlined: true,
    displayName: 'Mapped',
    color: 'nubeGreen',
  },
  INTEGRATED: {
    name: 'INTEGRATED',
    outlined: false,
    displayName: 'Integrated',
    color: 'nubeGreen',
  },
  UNINTEGRATED: {
    name: 'UNINTEGRATED',
    outlined: false,
    displayName: 'Not Integrated',
    color: 'red',
  },
};
export default {
  name: 'app-status-chip',
  props: ['status', 'state', 'chipSize'],
  computed: {
    currentStatus() {
      const appStatus = appStatusStates[this.state];
      if (!appStatus) {
        return {
          name: 'default',
          displayName: this.capitalize(this.state),
          color: 'nubeYellow',
        };
      }
      return appStatus;
    },
    defaultStatus() {
      return this.capitalize(this.state);
    },
  },
  methods: {
    capitalize(str) {
      if (!str) {
        str = '';
      }
      str = String(str);
      return str.charAt(0).toUpperCase() + str.slice(1);
    },
  },
};
</script>
