<template>
  <v-card :loading="isFetching">
    <v-card-title class="mb-5">
      <span>{{ displayName }}</span>
      <span>&nbsp;|&nbsp;</span>
      <span class="subtitle-2">Timer Configure</span>
    </v-card-title>
    <v-card-text>
      <v-form ref="form" v-model="formValid" lazy-validation>
        <v-row dense>
          <service-cron-picker v-if="isInitialized" v-model="minutesValue"></service-cron-picker>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <form-error :error="error"></form-error>
      <v-spacer />
      <v-btn text @click="onCancel">Cancel</v-btn>
      <v-btn color="primary" text v-on:click="onSave">Save</v-btn>
      <v-btn color="red" text v-on:click="onDelete">Delete</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import FormError from './FormError.vue';
import ServiceCronPicker from './ServiceCronPicker.vue';

export default {
  name: 'configure-cron-form',
  props: ['displayName', 'valid', 'isInitialized', 'minutes', 'error', 'onSave', 'onCancel', 'onDelete', 'isFetching'],
  components: { FormError, ServiceCronPicker },
  computed: {
    minutesValue: {
      get() {
        return this.minutes;
      },
      set(value) {
        this.$emit('updateMinutes', value);
      },
    },
    formValid: {
      get() {
        return this.valid;
      },
      set(value) {
        this.$emit('updateValid', value);
      },
    },
  },
};
</script>
